  /*height: 91px;*/\n  border: none;\n}\n.container .v-feed .v-feed__img {\n  margin-top: -0.08rem;\n  margin-left: 0;\n}\n.container .v-feed .v-feed__num {\n  top: 0;\n  left: 1.066667rem;\n  border: 1px solid #fff;\n}\n.container .v-feed .v-feed__sub {\n  margin-top: 0.186667rem;\n}\n.container .v-feed .v-feed__tit {\n  height: 0.48rem;\n}\n.container .v-feed .v-feed__sub.item-info {\n  margin-top: 0.186667rem;\n  height: 0.4rem;\n}\n.container .v-feed.border-bottom-1px:last-child:before,\n.container .v-feed.border-bottom-1px:last-child:after {\n  display: none;\n}\n.container .v-feed-info {\n  top: -0.026667rem;\n}\n.container .sp-time-info {\n  top: 0.613333rem;\n}\n.container .header-tab {\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 1.173333rem;\n  background-color: #fff;\n  border-bottom: 1px solid #e1e1e1;\n  /*tab切换*/\n  /*筛选按钮*/\n}\n.container .header-tab .tab {\n  float: left;\n  width: 88%;\n  height: 100%;\n  text-align: center;\n}\n.container .header-tab .tab .tab-btn {\n  float: left;\n  width: 50%;\n  font-family: PingFangSC-Regular;\n  font-size: 0.4rem;\n  line-height: 1.173333rem;\n  color: #606060;\n  /*发布切换按钮*/\n  /*接收切换按钮*/\n}\n.container .header-tab .tab .tab-btn .issue-btn {\n  position: relative;\n  bottom: -0.053333rem;\n  display: inline-block;\n  height: 1.12rem;\n}\n.container .header-tab .tab .tab-btn .issue-btn.active {\n  color: #C3303A;\n  border-bottom: 4px solid #C3303A;\n}\n.container .header-tab .tab .tab-btn .receive-btn {\n  position: relative;\n  bottom: -0.053333rem;\n  display: inline-block;\n  height: 1.12rem;\n}\n.container .header-tab .tab .tab-btn .receive-btn.active {\n  color: #C3303A;\n  border-bottom: 4px solid #C3303A;\n}\n.container .header-tab .filter {\n  position: relative;\n  padding-top: 0.133333rem;\n  float: right;\n  width: 12%;\n  height: 100%;\n}\n.container .header-tab .filter:after {\n  position: absolute;\n  content: '';\n  top: 0.293333rem;\n  width: 0;\n  height: 0.533333rem;\n  border-left: 1px solid #ccc;\n}\n.container .header-tab .filter .filter-icon {\n  margin: 0 auto;\n  width: 0.4rem;\n  height: 0.853333rem;\n  background: url(\"../assets/icon-filter.png\") center center no-repeat;\n  background-size: contain;\n}\n.container .content {\n  position: relative;\n  /*padding-bottom: 30px;*/\n  width: 100%;\n  height: 100%;\n  background-color: #f2f2f4;\n}\n.container .content .list-info {\n  width: 100%;\n  background: #fff;\n  /*时间信息*/\n}\n.container .content .list-info .time-info {\n  padding: 0.32rem 0 0 0.4rem;\n  width: 100%;\n  height: 0.986667rem;\n  background-color: #f2f2f4;\n  /*时间信息文本*/\n}\n.container .content .list-info .time-info .time-text {\n  margin-right: 0.133333rem;\n  font-family: PingFangSC-Regular;\n  font-size: 0.373333rem;\n  color: #777E8C;\n}\n.container .content.tabFirst {\n  height: 0;\n  padding-bottom: 0;\n}\n.container .content.tabSecond {\n  height: 0;\n  padding-bottom: 0;\n}\n.container .issue {\n  position: fixed;\n  right: 0.186667rem;\n  bottom: 1.146667rem;\n  /*width: 46px;*/\n  /*height: 46px;*/\n  width: 1.733333rem;\n  height: 1.733333rem;\n  background: url(\"../assets/icon-issue.png\") center center no-repeat;\n  background-size: 100%;\n  z-index: 3;\n}\n.container .issue.active {\n  -webkit-transition: all 0.5s;\n  transition: all 0.5s;\n  transform: rotate(45deg);\n  -ms-transform: rotate(45deg);\n  /* IE 9 */\n  -moz-transform: rotate(45deg);\n  /* Firefox */\n  -webkit-transform: rotate(45deg);\n  /* Safari 和 Chrome */\n  -o-transform: rotate(45deg);\n  /* Opera */\n}\n.container .issue-category {\n  position: absolute;\n  left: 0;\n  bottom: 0;\n  width: 100%;\n  height: 5.333333rem;\n  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0)), color-stop(54%, rgba(255, 255, 255, 0.9)));\n  background-image: linear-gradient(-180deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.9) 54%);\n  z-index: 2;\n  /*成绩按钮*/\n  /*作业按钮*/\n  /*通知按钮*/\n  /* 淡入-从右 */\n}\n.container .issue-category .grade-btn {\n  position: absolute;\n  top: 2.4rem;\n  left: 1.973333rem;\n  -webkit-animation: fadeinR 0.5s;\n  animation: fadeinR 0.5s;\n}\n.container .issue-category .grade-btn img {\n  width: 2.026667rem;\n  height: 2.026667rem;\n}\n.container .issue-category .grade-btn .p-text {\n  margin-top: -0.266667rem;\n  font-size: 0.373333rem;\n  text-align: center;\n  color: #606060;\n}\n.container .issue-category .grade-btn .p-text.gray {\n  color: #c8c8c8;\n}\n.container .issue-category .word-btn {\n  position: absolute;\n  top: 2.4rem;\n  left: 6rem;\n  -webkit-animation: fadeinR 0.5s;\n  animation: fadeinR 0.5s;\n}\n.container .issue-category .word-btn img {\n  width: 2.026667rem;\n  height: 2.026667rem;\n}\n.container .issue-category .word-btn .p-text {\n  margin-top: -0.266667rem;\n  font-size: 0.373333rem;\n  text-align: center;\n  color: #606060;\n}\n.container .issue-category .issue-btn {\n  position: absolute;\n  top: 2.4rem;\n  left: 4rem;\n  -webkit-animation: fadeinR 0.5s;\n  animation: fadeinR 0.5s;\n}\n.container .issue-category .issue-btn img {\n  width: 2.026667rem;\n  height: 2.026667rem;\n}\n.container .issue-category .issue-btn .p-text {\n  margin-top: -0.266667rem;\n  font-size: 0.373333rem;\n  text-align: center;\n  color: #606060;\n}\n.container .issue-category .issue-btn .p-text.gray {\n  color: #c8c8c8;\n}\n@-webkit-keyframes fadeinR {\n0% {\n    opacity: 0;\n    -webkit-transform: translateX(2.666667rem);\n}\n100% {\n    opacity: 1;\n    -webkit-transform: translateX(0);\n}\n}\n@keyframes fadeinR {\n0% {\n    opacity: 0;\n    -webkit-transform: translateX(2.666667rem);\n    transform: translateX(2.666667rem);\n}\n100% {\n    opacity: 1;\n    -webkit-transform: translateX(0);\n    transform: translateX(0);\n}\n}\n.container .no-notice {\n  position: relative;\n  width: 100%;\n  height: 100%;\n  background-color: #fff;\n}\n.container .no-notice .notice {\n  position: absolute;\n  top: 2.186667rem;\n  left: 3.413333rem;\n  width: 3.2rem;\n  height: 3.2rem;\n}\n.container .no-notice .notice .notice-img {\n  display: block;\n  margin: 0 auto;\n  width: 100%;\n  height: 100%;\n}\n.container .no-notice .notice .notice-info {\n  margin-top: 0.8rem;\n  font-size: 0.453333rem;\n  text-align: center;\n  color: #606060;\n}\n.container .filter-popup {\n  display: none;\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  min-height: 100%;\n  overflow: hidden;\n  z-index: 3;\n  background-color: rgba(0, 0, 0, 0.3);\n  /*蒙版*/\n}\n.container .filter-popup .select-wrap {\n  display: none;\n  /*选择区域*/\n  /*提交按钮区域*/\n}\n.container .filter-popup .select-wrap .select-area {\n  padding: 0.533333rem 0.4rem 0 0.4rem;\n  width: 100%;\n  height: 9.306667rem;\n  background-color: #fff;\n  /*选择类型*/\n}\n.container .filter-popup .select-wrap .select-area .select-type {\n  padding-bottom: 0.133333rem;\n  border-bottom: 1px solid #dddee3;\n  /*flex 布局*/\n}\n.container .filter-popup .select-wrap .select-area .select-type .p-text {\n  font-size: 0.373333rem;\n  color: #383838;\n}\n.container .filter-popup .select-wrap .select-area .select-type .v-checkbox dd {\n  display: -webkit-box;\n  display: -ms-flexbox;\n  display: flex;\n  -webkit-box-pack: justify;\n  -ms-flex-pack: justify;\n  justify-content: space-between;\n  -ms-flex-wrap: wrap;\n  flex-wrap: wrap;\n}\n.container .filter-popup .select-wrap .select-area .select-type .v-checkbox.tags label .v-checkbox__label {\n  margin-right: 0;\n}\n.container .filter-popup .select-wrap .select-area .select-class {\n  padding: 0.4rem 0 0.533333rem 0;\n  /*flex 布局*/\n}\n.container .filter-popup .select-wrap .select-area .select-class .p-text {\n  font-size: 0.373333rem;\n  color: #383838;\n}\n.container .filter-popup .select-wrap .select-area .select-class .v-checkbox dd {\n  display: -webkit-box;\n  display: -ms-flexbox;\n  display: flex;\n  /*justify-content: space-between;*/\n  -ms-flex-wrap: wrap;\n  flex-wrap: wrap;\n}\n.container .filter-popup .select-wrap .select-area .select-class .v-checkbox.tags label .v-checkbox__label {\n  margin-right: 0.4rem;\n}\n.container .filter-popup .select-wrap .select-area .select-class .v-checkbox.tags label:nth-child(3n) .v-checkbox__label {\n  margin-right: 0;\n}\n.container .filter-popup .select-wrap .submit-area {\n  padding: 0.213333rem 0.4rem;\n  width: 100%;\n  height: 1.44rem;\n  background-color: #fff;\n  border-top: 1px solid #dddee3;\n  border-bottom: 1px solid #dddee3;\n  /*取消按钮*/\n  /*确认按钮*/\n}\n.container .filter-popup .select-wrap .submit-area .cancel-btn {\n  float: left;\n  width: 4.4rem;\n  height: 1.013333rem;\n  line-height: 1.013333rem;\n  text-align: center;\n  font-size: 0.453333rem;\n  border-radius: 0.106667rem;\n  border: 1px solid #C3303A;\n  color: #C3303A;\n  background-color: #fff;\n}\n.container .filter-popup .select-wrap .submit-area .sure-btn {\n  float: right;\n  width: 4.4rem;\n  height: 1.013333rem;\n  line-height: 1.013333rem;\n  text-align: center;\n  font-size: 0.453333rem;\n  border-radius: 0.106667rem;\n  border: 1px solid #C3303A;\n  color: #fff;\n  background-color: #C3303A;\n}\n.container .filter-popup .select-wrap.show {\n  display: block;\n  -webkit-animation: fadeinT 0.5s;\n  animation: fadeinT 0.5s;\n}\n.container .filter-popup .select-wrap.hide {\n  display: none;\n  -webkit-animation: fadeoutT 0.5s;\n  animation: fadeoutT 0.5s;\n}\n.container .filter-popup .mask {\n  position: absolute;\n  /*top: 403px;*/\n  width: 100%;\n  height: 100%;\n  /*background-color: rgba(0,0,0,0.3);*/\n}\n.container .filter-popup.show {\n  display: block;\n}\n.container .filter-popup.hide {\n  display: none;\n}\n@-webkit-keyframes fadeinT {\n0% {\n    opacity: 0;\n    -webkit-transform: translateY(-2.666667rem);\n}\n100% {\n    opacity: 1;\n    -webkit-transform: translateY(0);\n}\n}\n@keyframes fadeinT {\n0% {\n    opacity: 0;\n    -webkit-transform: translateY(-2.666667rem);\n    transform: translateY(-2.666667rem);\n}\n100% {\n    opacity: 1;\n    -webkit-transform: translateY(0);\n    transform: translateY(0);\n}\n}\n@-webkit-keyframes fadeoutT {\n0% {\n    opacity: 1;\n    -webkit-transform: translateY(0);\n}\n100% {\n    opacity: 0;\n    -webkit-transform: translateY(-2.666667rem);\n}\n}\n@keyframes fadeoutT {\n0% {\n    opacity: 1;\n    -webkit-transform: translateY(0);\n    transform: translateY(0);\n}\n100% {\n    opacity: 0;\n    -webkit-transform: translateY(-2.666667rem);\n    transform: translateY(-2.666667rem);\n}\n}\n.container .after-trigger span {\n  color: #9C9C9C;\n}\n.container .before-trigger span {\n  color: #9C9C9C;\n}"],"sourceRoot":""}]);

