<template>
  <!--内容区域-->
    <div class="container">
      <!--头部tab切换-->
      <div class="header-tab">
        <div class="tab">
          <div class="tab-btn">
            <span class="issue-btn" :class="{'active': curIssueActive}" @click="tabClick(0)">我发布的</span>
          </div>
          <div class="tab-btn">
            <span class="receive-btn" :class="{'active': curReceiveActive}" @click="tabClick(1)">我接收的</span>
          </div>
        </div>
        <!--筛选漏斗-->
        <div class="filter" @click="filterClick">
          <div class="filter-icon"></div>
        </div>
      </div>
      <!--中心区域内容-->
      <div class="content" :class="{'tabFirst' : issueList.length === 0}" v-if="showIssue">
        <!--每一项列表信息-->
        <cube-scroll  ref="scrollOne" :options="options" :refreshDelay= 50 @pulling-down="onPullingDown" @pulling-up="onPullingUp" :data="issueList">
          <div class="list-info" v-for="item in issueList">
            <div class="time-info">
              <span class="time-text" v-text="item.dateInfo"></span>
            </div>
            <v-feed v-for="(itemList, index) in item.notices"
              :key="index"
              :id="itemList.noId"
              :currentType="0"
              :imgUrl = "getIconUrl(itemList.noType)"
              :title = "itemList.noTitle"
              :subtitle = "itemList.className"
              :time = "getTime(itemList.noReceipt,itemList.noReceiptTime)"
              :receiptInfo="getIssueInfo(itemList.noReceipt,itemList.noReceiptTime)"
              :msgNum="1"
              :itemInfo="getItemInfo(itemList.noPicture,itemList.noContent)"
              :showRelation="false"
              :currentReceipt="itemList.noReceipt"
              :noType="itemList.noType"
            > </v-feed>
          </div>
        </cube-scroll>
      </div>
      <div class="content" :class="{'tabSecond' : receiveList.length === 0}" v-if="showReceive">
        <!--每一项列表信息-->
        <cube-scroll  ref="scrollTwo" :options="options" :refreshDelay= 50 @pulling-down="pullingDown" @pulling-up="pullingUp" :data="receiveList">
          <div class="list-info" v-for="item in receiveList">
            <div class="time-info">
              <span class="time-text">{{item.dateInfo}}</span>
            </div>
            <v-feed v-for="(receiveData,index) in item.notices"
               :key="index"
              :id="receiveData.noId"
              :noType="receiveData.noType"
              :currentType="1"
              :imgUrl = "getIconUrl(receiveData.noType)"
              :title = "receiveData.noTitle"
              :subtitle = "'发布人：'+receiveData.publisher"
              :time = "getTime(receiveData.noReceipt,receiveData.noReceiptTime)"
              :msgNum="receiveData.nrConsult"
              :receiptInfo="getReceiptInfo(receiveData.noReceipt,receiveData.noReceiptTime,receiveData.nrReceipt)"
              :itemInfo="getItemInfo(receiveData.noPicture,receiveData.noContent)"
              :showRelation="false"
            > </v-feed>
          </div>
        </cube-scroll>
      </div>
      <!--发布按钮-->
      <div class="issue" :class="{'active' :issueActive}" @click="issueClick" v-show="showIssueFlag === 2"></div>
      <!--发布信息分类-->
      <div class="issue-category" v-show="showCategory">
        <!--成绩-->
        <div class="grade-btn" @click="goGrade">
          <img src="../assets/grade-btn.png" v-show="teacherType === 1">
          <img src="../assets/grade-gray-btn.png" v-show="teacherType === 0">
          <p class="p-text" :class="{'gray': teacherType === 0}">成绩</p>
        </div>
        <!--通知-->
        <div class="issue-btn" @click="goIssue">
          <img src="../assets/issue-btn.png" v-show="teacherType === 1">
          <img src="../assets/issue-gray-btn.png" v-show="teacherType === 0">
          <p class="p-text" :class="{'gray': teacherType === 0}">通知</p>
        </div>
        <!--作业-->
        <div class="word-btn" @click="goWord">
          <img src="../assets/work-btn.png">
          <p class="p-text">作业</p>
        </div>
      </div>
      <!--无通知提示-->
      <div class="no-notice" v-show="(!issueList.length && actIndex === 0) || (!receiveList.length && actIndex === 1)">
        <div class="notice">
          <img src="../assets/icon-notice.png" class="notice-img">
          <p class="notice-info">暂无通知</p>
        </div>
        <!--发布按钮-->
        <div class="issue" :class="{'active' :issueActive}" @click="issueClick" v-show="showIssueFlag === 2"></div>
        <!--发布信息分类-->
        <div class="issue-category" v-show="showCategory">
          <!--成绩-->
          <div class="grade-btn" @click="goGrade">
            <img src="../assets/grade-btn.png" v-show="teacherType === 1">
            <img src="../assets/grade-gray-btn.png" v-show="teacherType === 0">
            <p class="p-text" :class="{'gray': teacherType === 0}">成绩</p>
          </div>
          <!--通知-->
          <div class="issue-btn" @click="goIssue">
            <img src="../assets/issue-btn.png" v-show="teacherType === 1">
            <img src="../assets/issue-gray-btn.png" v-show="teacherType === 0">
            <p class="p-text" :class="{'gray': teacherType === 0}">通知</p>
          </div>
          <!--作业-->
          <div class="word-btn" @click="goWord">
            <img src="../assets/work-btn.png">
            <p class="p-text">作业</p>
          </div>
        </div>
      </div>
      <!--筛选弹出层-->
      <div class="filter-popup" :class="{'show' : showPopUp, 'hide' : hidePopUp}">
        <div class="select-wrap" :class="{'show' : showPopUp, 'hide' : hidePopUp}">
        <!--选择区域-->
        <div class="select-area">
          <!--选择类型-->
          <div class="select-type">
            <p class="p-text">选择类型</p>
            <v-checkbox v-model="selectType" :options="selectDataType" :tagsMode="true"></v-checkbox>
          </div>
          <!--选择班级-->
          <div class="select-class" v-if="selectDataClass.length > 0">
            <p class="p-text">选择班级</p>
            <v-checkbox v-model="selectClass" :options="selectDataClass" :tagsMode="true"></v-checkbox>
          </div>
        </div>
        <!--确认取消按钮区域-->
        <div class="submit-area">
          <div class="cancel-btn" @click="hidePopUpClick">取消</div>
          <div class="sure-btn" @click="filterList">确认</div>
        </div>
        </div>
        <!--蒙版-->
        <div class="mask" @click="hidePopUpClick"></div>
      </div>
    </div>
</template>

<script>
import api from '../utils/api';
import {Style, Scroll} from 'cube-ui';
import vFeed from '../vendor/v-feed';
import vCheckbox from '../vendor/v-checkbox';
import config from '../config';
import { mapGetters, mapMutations } from 'vuex';
export default {
  name: 'notify-list',
  components: {
    Style,
    cubeScroll: Scroll,
    vFeed,
    'v-checkbox': vCheckbox
  },
  data () {
    return {
      options: {
        observeDOM: true,
        pullDownRefresh: {
          threshold: 90,
          stop: 100,
          txt: '刷新成功'
        },
        pullUpLoad: {
          threshold: 0,
          txt: {
            more: '加载中',
            noMore: '我是有底线的'
          }
        }
      },
      teacherType: 1, // 老师类别  1 班主任/ 0 任课老师
      actIndex: 0,
      showTips: false, // 是否展示搜索为空
      selectDataClass: [], // 筛选班级数据
      showIssue: true, // 我的发布
      showReceive: false, // 我的接收
      curIssueActive: true, // 激活我的发布页面
      curReceiveActive: false, // 激活我的接收页面
      showCategory: false, // 我的发布类别按钮激活
      issueActive: false, // 发布按钮切换状态
      selectType: [], // 选择类型 默认选中
      selectTypeTemp: [], // 选择类型 默认选中
      selectClass: [], // 选择班级 默认选中
      selectClassTemp: [], // 选择班级 默认选中
      showPopUp: false, // 筛选弹框
      hidePopUp: true, // 筛选弹框
      issueList: [], // 我的发布通知列表数据
      receiveList: [], // 我的接收通知列表数据
      pageNum: 1, // 当前页数 (第一个tab切换页)
      pages: 0, // 总页数 (第一个tab切换页)
      size: 0, // 当前页显示条数 (第一个tab切换页)
      pageSize: 0, // 每页显示条数 (第一个tab切换页)
      secondPageNum: 1, // 当前页数 (第一个tab切换页)
      secondPages: 0, // 总页数 (第一个tab切换页)
      secondSize: 0, // 当前页显示条数 (第一个tab切换页)
      secondPageSize: 0, // 每页显示条数 (第一个tab切换页)
      userTypeObj: {
        student: 0,
        teacher: 2,
        parent: 1
      },
      secondPageNumParams: {
        pageNumber: 1,
        pageSize: 20,
        // classId: null,
        noTypes: null
      },
      firstPageNumParams: {
        pageNumber: 1,
        pageSize: 20,
        classIds: null,
        noTypes: null
      },
      showIssueFlag: 1 // 根据身份区分 是否显示发布+按钮 只有老师身份的显示（学生家长都不显示）
    };
  },
  computed: {
    ...mapGetters(['getOriginType'])
  },
  created () {
    this.selectDataType = [
      {
        'type': '通知',
        'id': 0
      },
      {
        'type': '作业',
        'id': 1
      },
      {
        'type': '成绩',
        'id': 2
      }
    ];
    this.showIssueFlag = this.userTypeObj[JSON.parse(decodeURIComponent(config.userType)).userType];
    // 获取教师类型
    this.teacherType = config.teacherType;
    // 请求列表接口
    this.getIssueList('down');
    this.getReceiveList('down');
    this.getClassesList();
  },
  activated () {
    this.SET_ORIGIN_TYPE('listPage');
  },
  methods: {
    ...mapMutations(['SET_ORIGIN_TYPE']),
    // 第一个页签 下拉刷新
    onPullingDown () {
      this.firstPageNumParams.pageNumber = 1;
      this.getIssueList('down');
    },
    // 第一个页签 上拉刷新
    onPullingUp () {
      if (this.firstPageNumParams.pageNumber < this.pages) {
        this.firstPageNumParams.pageNumber += 1;
        this.getIssueList();
      } else {
        this.$refs.scrollOne.forceUpdate();
      }
    },
    // 第二个页签 下拉刷新
    pullingDown () {
      this.secondPageNumParams.pageNumber = 1;
      this.getReceiveList('down');
    },
    // 第二个页签 上拉刷新
    pullingUp () {
      if (this.secondPageNumParams.pageNumber < this.secondPages) {
        this.secondPageNumParams.pageNumber += 1;
        this.getReceiveList();
      } else {
        this.$refs.scrollTwo.forceUpdate();
      }
    },
    // tab点击切换
    tabClick (num) {
      this.actIndex = num;
      if (num === 0) {
        this.selectType = []; // 清空筛选状态
        this.selectClass = [];// 清空筛选状态
        this.firstPageNumParams.noTypes = null;
        this.firstPageNumParams.classIds = null;
        this.firstPageNumParams.pageNumber = 1;
        this.getIssueList('down'); // 请求列表接口
        setTimeout(() => {
          this.$refs.scrollOne.refresh();
          this.$refs.scrollOne.scrollTo(0, 0);
        }, 0);
        this.showIssue = true;
        this.showReceive = false;
        this.curIssueActive = true;
        this.curReceiveActive = false;
        //  重置 发布按钮状态
        this.showCategory = false;
        this.issueActive = false;
      } else {
        this.selectType = []; // 清空筛选状态
        this.selectClass = [];// 清空筛选状态
        this.secondPageNumParams.noTypes = null;
        // this.secondPageNumParams.classId = null;
        this.secondPageNumParams.pageNumber = 1;
        this.getReceiveList('down'); // 请求列表接口
        setTimeout(() => {
          this.$refs.scrollTwo.refresh();
          this.$refs.scrollTwo.scrollTo(0, 0);
        }, 0);
        this.showReceive = true;
        this.showIssue = false;
        this.curReceiveActive = true;
        this.curIssueActive = false;
        //  重置 发布按钮状态
        this.showCategory = false;
        this.issueActive = false;
      }
    },
    //  发布按钮点击
    issueClick () {
      this.issueActive = !this.issueActive;
      this.showCategory = !this.showCategory;
    },
    //  筛选按钮点击
    filterClick () {
      this.selectTypeTemp = JSON.parse(JSON.stringify(this.selectType));
      this.selectClassTemp = JSON.parse(JSON.stringify(this.selectClass));
      // 展开弹框
      this.showPopUp = true;
      this.hidePopUp = false;
    },
    //  取消筛选弹框
    hidePopUpClick () {
      this.selectType = JSON.parse(JSON.stringify(this.selectTypeTemp));
      this.selectClass = JSON.parse(JSON.stringify(this.selectClassTemp));
      this.hidePopUp = true;
      this.showPopUp = false;
    },
    //  路由跳转
    //  跳转成绩发布页
    goGrade () {
      if (this.teacherType === 1) {
        this.$router.push({
          path: '/publishScore',
          query: {
            noType: 2,
            origin: this.getOriginType
          }
        });
        this.issueActive = false;
        this.showCategory = false;
      }
    },
    // 跳转发布作业
    goWord () {
      this.$router.push({
        path: '/publishHomework',
        query: {
          noType: 1,
          origin: this.getOriginType
        }
      });
      this.issueActive = false;
      this.showCategory = false;
    },
    // 跳转 发布通知页面
    goIssue () {
      if (this.teacherType === 1) {
        this.$router.push({
          path: '/publishNotice',
          query: {
            noType: 0,
            origin: this.getOriginType
          }
        });
        this.issueActive = false;
        this.showCategory = false;
      }
    },
    // 获取通知列表数据 (我发布的)
    getIssueList (type) {
      let params = this.firstPageNumParams;
      api.getPublisher(params).then((res) => {
        if (res && res.data && res.data.data) {
          const data = res.data.data;
          if (type === 'down') {
            // 获取我的发布列表数据
            this.issueList = data.list;
            this.pages = data.pages;
            this.size = data.size;
            this.pageSize = data.pageSize;
          } else {
            this.issueList = this.issueList.concat(data.list);
            this.pages = data.pages;
            this.size = data.size;
            this.pageSize = data.pageSize;
          }
          console.log(this.issueList);
        }
      }, (err) => {
        console.log(err);
      });
    },
    // 获取通知列表数据 (我接收的)
    getReceiveList (type) {
      let params = this.secondPageNumParams;
      api.getInformReceive(params).then((res) => {
        if (res && res.data && res.data.data) {
          const data = res.data.data;
          if (type === 'down') {
            // 获取我的发布列表数据
            this.receiveList = data.list;
            this.secondPages = data.pages;
            this.secondSize = data.size;
            this.secondPageSize = data.pageSize;
          } else {
            this.receiveList = this.receiveList.concat(data.list);
            this.secondPages = data.pages;
            this.secondSize = data.size;
            this.secondPageSize = data.pageSize;
          }
        }
      }, (err) => {
        console.log(err);
      });
    },
    /**
     * 获取头像url 类别
     * @param type
     */
    getIconUrl (type) {
      let url = '';
      if (type === 0) {
        url = require('../assets/icon-tz.png');
      } else if (type === 1) {
        url = require('../assets/icon-zy.png');
      } else {
        url = require('../assets/icon-cj.png');
      }
      return url;
    },
    // 获取回执时间 (为了处理选中回执按钮，但不选择时间，后台会返回最大时间)
    // 253402271639000 时间戳是 后台返回的时间最大值
    getTime (noReceipt, noReceiptTime) {
      let [time] = [0];
      if (noReceipt === 0) {
        time = 0;
      } else if (noReceipt === 1 && noReceiptTime && noReceiptTime !== 253402271639000) {
        time = noReceiptTime;
      } else if (noReceipt === 1 && noReceiptTime && noReceiptTime === 253402271639000) {
        time = 0;
      }
      return {time};
    },
    /**
     * 判断是否需要显示回执状态 （根据回执状态 及回执时间判断）(我的发布下面的)
     * @param receipt
     * @param receiptTime
     */
    getIssueInfo (receipt, receiptTime) {
      let [flag, receiptImg, setTimeColor] = ['', '', false];
      // 获取当前系统时间戳
      var timestamp = (new Date()).valueOf();
      if (receipt === 0) {
        flag = '-1';
        receiptImg = '';
        setTimeColor = false;
      } else if (receipt === 1 && !receiptTime) {
        flag = '0';
        receiptImg = require('../assets/icon-xhz.png');
        setTimeColor = false;
      } else if (receipt === 1 && receiptTime === 253402271639000) {
        flag = '0';
        receiptImg = require('../assets/icon-xhz.png');
        setTimeColor = false;
      } else if (receipt === 1 && receiptTime && receiptTime !== 253402271639000 && receiptTime > timestamp) {
        flag = '1';
        receiptImg = require('../assets/icon-xshz.png');
        setTimeColor = false;
      } else if (receipt === 1 && receiptTime && receiptTime !== 253402271639000 && receiptTime < timestamp) {
        flag = '0';
        receiptImg = require('../assets/icon-yjz.png');
        setTimeColor = true;
      }
      return {flag, receiptImg, setTimeColor};
    },
    /**
     * 判断是否需要显示回执状态 （根据回执状态 及回执时间判断）（我的接收下面的）
     * @param receipt
     * @param receiptTime
     */
    getReceiptInfo (receipt, receiptTime, nrReceipt) {
      let [flag, receiptImg, setTimeColor] = ['', '', false];
      // 获取当前系统时间戳
      var timestamp = (new Date()).valueOf();
      if (receipt === 0) {
        flag = '-1';
        receiptImg = '';
        setTimeColor = false;
      } else if (receipt === 1 && nrReceipt === 0 && !receiptTime) {
        flag = '0';
        receiptImg = require('../assets/icon-xhz.png');
        setTimeColor = false;
      } else if (receipt === 1 && nrReceipt === 0 && receiptTime === 253402271639000) {
        flag = '0';
        receiptImg = require('../assets/icon-xhz.png');
        setTimeColor = false;
      } else if (receipt === 1 && nrReceipt === 1 && receiptTime && receiptTime === 253402271639000) {
        flag = '0';
        receiptImg = require('../assets/icon-yhz.png');
        setTimeColor = true;
      } else if (receipt === 1 && nrReceipt === 1 && receiptTime && receiptTime !== 253402271639000) {
        flag = '0';
        receiptImg = require('../assets/icon-yhz.png');
        setTimeColor = true;
      } else if (receipt === 1 && nrReceipt === 0 && receiptTime && receiptTime !== 253402271639000 && receiptTime > timestamp) {
        flag = '1';
        receiptImg = require('../assets/icon-xshz.png');
        setTimeColor = false;
      } else if (receipt === 1 && nrReceipt === 0 && receiptTime && receiptTime !== 253402271639000 && receiptTime < timestamp) {
        flag = '0';
        receiptImg = require('../assets/icon-yjz.png');
        setTimeColor = true;
      }
      return {flag, receiptImg, setTimeColor};
    },
    /**
     * 获取当前名片显示的内容文本信息
     * @param noPicture
     * @param noContent
     * @returns {string}
     */
    getItemInfo (noPicture, noContent) {
      var _info = '';
      if (noPicture === 0) {
        _info = noContent;
      } else if (noPicture === 1) {
        _info = '[图片]' + noContent;
      }
      return _info;
    },
    /**
     * 筛选列表list
     */
    filterList () {
      this.hidePopUp = true;
      this.showPopUp = false;
      var _selectType = this.selectType;
      var _selectClass = this.selectClass;
      var typeArr = [];
      var classArr = [];
      for (var i = 0;i < _selectType.length;i++) {
        var id = _selectType[i].id;
        typeArr.push(id);
      }
      var _typeArr = typeArr.join(',');
      for (var j = 0;j < _selectClass.length;j++) {
        var _classId = _selectClass[j].classId;
        classArr.push(_classId);
      }
      var _classArr = classArr.join(',');

      // 判断 如果一个都不选  默认请求全部
      if (typeArr.length === 0 && classArr.length === 0) {
        this.hidePopUp = true;
        this.showPopUp = false;
      }
      // 我的发布页签里的筛选
      if (this.showIssue) {
        this.firstPageNumParams.pageNumber = 1;
        this.firstPageNumParams.classIds = _classArr;
        this.firstPageNumParams.noTypes = _typeArr;
        this.getIssueList('down');
        this.$refs.scrollOne.refresh();
        this.$refs.scrollOne.scrollTo(0, 0);
        // 我的接收里的筛选
      } else if (this.showReceive) {
        this.secondPageNumParams.pageNumber = 1;
        // this.secondPageNumParams.classId = _classArr;
        this.secondPageNumParams.noTypes = _typeArr;
        this.getReceiveList('down');
        this.$refs.scrollTwo.refresh();
        this.$refs.scrollTwo.scrollTo(0, 0);
      }
    },
    /**
     * 获取班级列表
     */
    getClassesList () {
      let params = {};
      api.getClassList(params).then((data) => {
        this.selectDataClass = data.data.data;
        if (!this.selectDataClass) {
          this.selectDataClass = [];
        }
        console.log(this.selectDataClass);
      }, (err) => {
        console.log(err);
      });
    }
  },
  mounted () {}
};
</script>

<style lang="scss">
.container{
  position: relative;
  padding-top: 44px;
  width: 100%;
  height: 100%;
  overflow: scroll;
  /*覆盖feed 组件样式*/
  .v-feed{
    margin-left: 15px;
    min-height: 91px;
    /*height: 91px;*/
    border: none;
    .v-feed__img{
      margin-top: -3px;
      margin-left: 0;
    }
    .v-feed__num{
      top:0;
      left: 40px;
      border: 1px solid #fff; /*no*/
    }
    .v-feed__sub{
      margin-top: 7px;
    }
    .v-feed__tit{
      height: 18px;
    }
    .v-feed__sub.item-info{
      margin-top: 7px;
      height: 15px;
    }
  }
  .v-feed.border-bottom-1px:last-child:before,
  .v-feed.border-bottom-1px:last-child:after{
    display: none;
  }
  .v-feed-info{
    top:-1px;
  }
  .sp-time-info{
    top: 23px;
  }
  /*头部tab切换*/
  .header-tab{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 44px;
    background-color: #fff;
    border-bottom: 1px solid #e1e1e1;/*no*/
    /*tab切换*/
    .tab{
      float: left;
      width: 88%;
      height: 100%;
      text-align: center;
      .tab-btn{
        float: left;
        width: 50%;
        font-family: PingFangSC-Regular;
        font-size: 15px;
        line-height: 44px;
        color: #606060;
        /*发布切换按钮*/
        .issue-btn{
          position: relative;
          bottom: -2px;
          display: inline-block;
          height: 42px;
          &.active{
            color: #C3303A ;
            border-bottom: 4px solid #C3303A;/*no*/
          }
        }
        /*接收切换按钮*/
        .receive-btn{
          position: relative;
          bottom: -2px;
          display: inline-block;
          height: 42px;
          &.active{
            color: #C3303A ;
            border-bottom: 4px solid #C3303A;/*no*/
          }
        }
      }
    }
    /*筛选按钮*/
    .filter{
      position: relative;
      padding-top: 5px;
      float: right;
      width: 12%;
      height: 100%;
      &:after{
        position: absolute;
        content: '';
        top:11px;
        width: 0px;
        height: 20px;
        border-left: 1px solid #ccc;/*no*/
      }
      .filter-icon{
        margin: 0 auto;
        width: 15px;
        height: 32px;
        background: url("../assets/icon-filter.png") center center no-repeat;
        background-size: contain;
      }
    }
  }
  /*中心内容区域*/
  .content{
    position: relative;
    /*padding-bottom: 30px;*/
    width: 100%;
    height: 100%;
    background-color: #f2f2f4;
    .list-info{
      width: 100%;
      background: #fff;
      /*时间信息*/
      .time-info{
        padding: 12px 0 0 15px;
        width: 100%;
        height: 37px;
        background-color: #f2f2f4;
        /*时间信息文本*/
        .time-text{
          margin-right: 5px;
          font-family: PingFangSC-Regular;
          font-size: 14px;
          color: #777E8C;
        }
      }
    }

    &.tabFirst{
      height: 0;
      padding-bottom: 0;
    }
    &.tabSecond{
      height: 0;
      padding-bottom: 0;
    }
  }
  /*发布按钮*/
  .issue{
    position: fixed;
    right: 7px;
    bottom: 43px;
    /*width: 46px;*/
    /*height: 46px;*/
    width: 65px;
    height: 65px;
    background: url("../assets/icon-issue.png") center center no-repeat;
    background-size: 100%;
    z-index: 3;
    &.active{
      transition: all 0.5s;
      transform:rotate(45deg);
      -ms-transform:rotate(45deg); 	/* IE 9 */
      -moz-transform:rotate(45deg); 	/* Firefox */
      -webkit-transform:rotate(45deg); /* Safari 和 Chrome */
      -o-transform:rotate(45deg); 	/* Opera */
    }
  }
  /*发布类别分类*/
  .issue-category{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 200px;
    background-image: linear-gradient(-180deg, rgba(255,255,255,0.00) 0%, rgba(255,255,255,0.90) 54%);
    z-index: 2;
    /*成绩按钮*/
    .grade-btn{
      position: absolute;
      top: 90px;
      left: 74px;
      img{
        width: 76px;
        height: 76px;
      }
      .p-text{
        margin-top: -10px;
        font-size: 14px;
        text-align: center;
        color: #606060;
        &.gray{
          color: #c8c8c8;
        }
      }
      -webkit-animation:fadeinR 0.5s;
      -moz-animation:fadeinR 0.5s;
      -ms-animation:fadeinR 0.5s;
      animation:fadeinR 0.5s;
    }
    /*作业按钮*/
    .word-btn{
      position: absolute;
      top: 90px;
      left: 225px;
      img{
        width: 76px;
        height: 76px;
      }
      .p-text{
        margin-top: -10px;
        font-size: 14px;
        text-align: center;
        color: #606060;
      }
      -webkit-animation:fadeinR 0.5s;
      -moz-animation:fadeinR 0.5s;
      -ms-animation:fadeinR 0.5s;
      animation:fadeinR 0.5s;
    }
    /*通知按钮*/
    .issue-btn{
      position: absolute;
      top: 90px;
      left: 150px;
      img{
        width: 76px;
        height: 76px;
      }
      .p-text{
        margin-top: -10px;
        font-size: 14px;
        text-align: center;
        color: #606060;
        &.gray{
          color: #c8c8c8;
        }
      }
      -webkit-animation:fadeinR 0.5s;
      -moz-animation:fadeinR 0.5s;
      -ms-animation:fadeinR 0.5s;
      animation:fadeinR 0.5s;
    }
    /* 淡入-从右 */
    @-webkit-keyframes fadeinR{
      0%{opacity:0;-webkit-transform:translateX(100px);}
      100%{opacity:1;-webkit-transform:translateX(0);}
    }
    @-moz-keyframes fadeinR{
      0%{opacity:0;-moz-transform:translateX(100px);}
      100%{opacity:1;-moz-transform:translateX(0);}
    }
    @-ms-keyframes fadeinR{
      0%{opacity:0;-ms-transform:translateX(100px);}
      100%{opacity:1;-ms-transform:translateX(0);}
    }
    @keyframes fadeinR{
      0%{opacity:0;transform:translateX(100px);}
      100%{opacity:1;transform:translateX(0);}
    }
  }
  .no-notice{
    position: relative;
    width: 100%;
    height: 100%;
    background-color: #fff;
    .notice{
      position: absolute;
      top: 82px;
      left: 128px;
      width: 120px;
      height: 120px;
      .notice-img{
        display: block;
        margin: 0 auto;
        width: 100%;
        height: 100%;
      }
      .notice-info{
        margin-top: 30px;
        font-size: 17px;
        text-align: center;
        color: #606060;
      }
    }
  }
  /*筛选弹出层*/
  .filter-popup{
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100%;
    overflow: hidden;
    z-index: 3;
    background-color: rgba(0, 0, 0, 0.3);
    .select-wrap{
      display: none;
      /*选择区域*/
      .select-area{
        padding: 20px 15px 0px 15px;
        width: 100%;
        height: 349px;
        background-color: #fff;
        /*选择类型*/
        .select-type{
          padding-bottom: 5px;
          border-bottom: 1px solid #dddee3;/*no*/
          .p-text{
            font-size: 14px;
            color: #383838;
          }
          /*flex 布局*/
          .v-checkbox dd{
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
          }
          .v-checkbox.tags label .v-checkbox__label{
            margin-right: 0;
          }
        }
        .select-class{
          padding: 15px 0 20px 0;
          .p-text{
            font-size: 14px;
            color: #383838;
          }
          /*flex 布局*/
          .v-checkbox dd{
            display: flex;
            /*justify-content: space-between;*/
            flex-wrap: wrap;
          }
          .v-checkbox.tags label .v-checkbox__label{
            margin-right: 15px;
          }
          .v-checkbox.tags label:nth-child(3n) .v-checkbox__label{
            margin-right: 0;
          }
        }
      }
      /*提交按钮区域*/
      .submit-area{
        padding: 8px 15px;
        width: 100%;
        height: 54px;
        background-color: #fff;
        border-top: 1px solid #dddee3;/*no*/
        border-bottom: 1px solid #dddee3;/*no*/
        /*取消按钮*/
        .cancel-btn{
          float: left;
          width: 165px;
          height: 38px;
          line-height: 38px;
          text-align: center;
          font-size: 17px;
          border-radius: 4px;
          border: 1px solid #C3303A ; /*no*/
          color: #C3303A;
          background-color: #fff;
        }
        /*确认按钮*/
        .sure-btn{
          float: right;
          width: 165px;
          height: 38px;
          line-height: 38px;
          text-align: center;
          font-size: 17px;
          border-radius: 4px;
          border: 1px solid #C3303A; /*no*/
          color: #fff;
          background-color: #C3303A;
        }
      }
      &.show{
        display: block;
        -webkit-animation:fadeinT 0.5s;
        -moz-animation:fadeinT 0.5s;
        -ms-animation:fadeinT 0.5s;
        animation:fadeinT 0.5s;
      }
      &.hide{
        display: none;
        -webkit-animation:fadeoutT 0.5s;
        -moz-animation:fadeoutT 0.5s;
        -ms-animation:fadeoutT 0.5s;
        animation:fadeoutT 0.5s;
      }
    }
    /*蒙版*/
    .mask{
      position: absolute;
      /*top: 403px;*/
      width: 100%;
      height: 100%;
      /*background-color: rgba(0,0,0,0.3);*/
    }
  }

  .filter-popup.show{
    display: block;
  }
  .filter-popup.hide{
    display: none;
  }

  /*淡入- 自上而下*/
  @-webkit-keyframes fadeinT{
    0%{opacity:0;-webkit-transform:translateY(-100px);}
    100%{opacity:1;-webkit-transform:translateY(0);}
  }
  @-moz-keyframes fadeinT{
    0%{opacity:0;-moz-transform:translateY(-100px);}
    100%{opacity:1;-moz-transform:translateY(0);}
  }
  @-ms-keyframes fadeinT{
    0%{opacity:0;-ms-transform:translateY(-100px);}
    100%{opacity:1;-ms-transform:translateY(0);}
  }
  @keyframes fadeinT{
    0%{opacity:0;transform:translateY(-100px);}
    100%{opacity:1;transform:translateY(0);}
  }

  /* 淡出-自下而上 */
  @-webkit-keyframes fadeoutT{
    0%{opacity:1;-webkit-transform:translateY(0);}
    100%{opacity:0;-webkit-transform:translateY(-100px);}
  }
  @-moz-keyframes fadeoutT{
    0%{opacity:1;-moz-transform:translateY(0);}
    100%{opacity:0;-moz-transform:translateY(-100px);}
  }
  @-ms-keyframes fadeoutT{
    0%{opacity:1;-ms-transform:translateY(0);}
    100%{opacity:0;-ms-transform:translateY(-100px);}
  }
  @keyframes fadeoutT{
    0%{opacity:1;transform:translateY(0);}
    100%{opacity:0;transform:translateY(-100px);}
  }

  /*上拉加载 上拉刷新文字样式修改*/
  .after-trigger{
    span{
      color: #9C9C9C;
    }
  }
  .before-trigger{
    span{
      color: #9C9C9C;
    }
  }
}
</style>



